<template>
  <div class="Hot">
    <!-- <div class="hotloading" v-show="isshowloadingmusic"> -->
    <div class="hot_top">
      <div class="hot_img">
        <div class="hot_world"></div>
        <div class="hottime">更新时间:12月17日</div>
      </div>
    </div>
    <div class="loading" v-show="isshowloadingmusic"></div>
    <Hotmusic :hotdate="hotdate"  @newmusicone="hotdatahandle" :paused="$attrs.paused" :newindex="$attrs.newindex" :newmusic="$attrs.newmusic"/>
  </div>
</template>

<script>
import Hotmusic from "../components/Hotmusic.vue";

export default {
  components: { Hotmusic },

  data() {
    return {
      hotid: [],
      hotsongstr: "",
      hotdate: [],
      isshowloadingmusic: true,
      count: 0,
    };
  },
  methods: {
    hotdatahandle: function (item,index,hotdate) {
      this.$emit("newmusicone", item,index, hotdate);
    },
  },
  watch: {
    count: function (n) {
      if (n == 2) {
        this.isshowloadingmusic = false;
      }
    },
  },
  created() {
    this.isshowloadingmusic = true;
    this.axios
      .get("/top/list?idx=1")
      .then((response) => {
        this.hotid = response.data.playlist.trackIds.slice(0, 20);
        this.hotid = this.hotid.map((item) => {
          return item.id;
        });
        this.hotsongstr = this.hotid.join(",");
        this.axios
          .get("/song/detail?ids=" + this.hotsongstr)
          .then((response) => {
            return (this.hotdate = response.data.songs);
          })
          .finally(() => {
            this.count++;
          });
      })
      .finally(() => {
        this.count++;
      });
  },
};
</script>

