<template>
  <div class="lyric">
    <ul>
      <li v-for="(item, index) in newlyric" :key="index">
        {{ item.text }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["newlyric", "currentTime"],
  data() {
    return {
      speed: 0,
    };
  },
  watch: {
    currentTime: function () {
        var lilength= document.querySelector(".lyric ul").querySelectorAll("li").length;
      (this.speed = this.newlyric.findIndex((element) => {
        return element.time > this.currentTime;
      }));
      if(this.currentTime>this.newlyric[lilength-1].time){
            this.speed=lilength;
      }
      (document.querySelector(".lyric ul").style.marginTop =
          -30 * (this.speed - 1) + 100 + "px");
      document
        .querySelector(".lyric ul")
        .querySelectorAll("li")
        .forEach((e) => {
          e.style.color = "#FAF6F4";
        });
    
      document.querySelector(".lyric ul").querySelectorAll("li")[
        this.speed - 1
      ].style.color = "green";
    },
  },
};
</script>

<style lang="less">
.lyric {
  // margin-top: 100px;
  height: 50vh;
  overflow: hidden;
  ul {
    // line-height: 30px;
    text-align: center;
    li {
      height: 30px;
      min-width: 150px;
      text-align: center;
      //  white-space: nowrap;
    }
  }
}
</style>