<template>
<div class="title_top">
  <h3 class="title">歌曲列表</h3>
  <ul>
    <li
      v-for="(item,index) in apprecommentitem"
      :key="item.id"
      class="newmusic"
      @click="newidhandle(item,index,apprecommentitem)"
    >
       <div class="num">
        <span
          v-text="index < 9 ? '0' + ++index : ++index"
          class="numindex"
          :class="{active:index<3}"
        ></span>
      </div> 
      <div class="left">
        <div class="top">
          <span>{{ item.name }}</span>
        </div>
        <div class="bottom">
          <span>{{ item.ar[0].name }}-{{ item.al.name }}</span>
        </div>
      </div>
      <div class="right">
        <span :class="{'active': newindex==undefined?'':newmusic[newindex].id===item.id&&paused}"></span>
      </div>
    </li>
  </ul>
  </div>
</template>

<script>
export default {
  props: ["apprecommentitem","paused","newindex","newmusic"],
  data() {
    return {
    };
  },
  methods: {
    newidhandle:function(item,index,apprecommentitem){
        this.$emit('newmusicone',item,index-1,apprecommentitem);
    }
  },
};
</script>

<style scoped lang="less">
.title{
  // margin-top:5px ;
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
}
ul{
  margin-bottom: 60px;
}
.newmusic {
  display: flex;
  margin-bottom: 10px;
  border-bottom: 1px solid #f8f8f9;
  // padding-left: 10px;
  .num {
    display: flex;
    flex: 1 0 10vw;
    justify-content: center;
    align-items: center;
    .numindex {
      display: inline-block;
      font-size: 16px;
      &.active {
        color: red;
      }
    }
  }
  .left {
    flex: 1 0 80vw;
    display: flex;
    flex-direction: column;
    padding: 5px 0px;
    span{
      display: inline-block;
        max-width: 60vw;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .bottom {
