        padding-left: 15px;

        &::before {
          content: "";
          width: 12px;
          height: 1em;
          background: url("../../public/img/icon.png") no-repeat;
          background-size: 166px 97px;
          position: absolute;
          left: 0;
          top: 0.45em;
        }
      }
    }
  }
  .right {
    flex: 1 0 10vw;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      display: inline-block;
      width: 22px;
      height: 22px;
      background: url("../../public/img/icon.png") no-repeat;
      background-position: -24px 0;
      background-size: 166px 97px;
      //   display: inline-block;
      //   width: 22px;
      //   height: 22px;
      //   background: url("../../public/img/Bars.gif");
      //   background-size: 22px 22px;
    }
    span.active {
      display: inline-block;
      width: 22px;
      height: 22px;
      background: url("../../public/img/Bars.gif");
      background-size: 22px 22px;
    }
  }
}
</style>