<template>
  <div class="Songlistdetails" @mousewheel.prevent >
    <div class="Masked"></div>
    <img :src="newdata.al.picUrl" alt="" class="bg" />
    <div class="top">
      <div @click="$emit('isshowSongList', false)" class="pull-down"></div>
      <div class="name">
        <span>{{ newdata.name }}</span>
        <span>{{ newdata.ar[0].name }}</span>
      </div>
      <div class="share"></div>
    </div>
    <div class="newsong"  @click="ishowLyric=!ishowLyric">
      <div class="song" v-show="ishowLyric">
        <img :src="newdata.al.picUrl" alt="" />
      </div>
      <Lyric v-show="!ishowLyric" :newlyric="newlyric" :currentTime="currentTime"/>
    </div>
    
    <div class="iron">
      <div class="heart"></div>
      <div class="download"></div>
      <div class="sing"></div>
      <div class="comment"></div>
      <div class="more"></div>
    </div>
    <div class="input">
      <span>{{ current }}</span>
      <input
        type="range"
        :max="duration"
        min="0"
        step="1"
        @input="getValue"
        :value="currentTime"
      />
      <span>{{ durat }}</span>
    </div>
    <div class="control">
      <div class="pattern"></div>
      <div class="Previous" @click="Previoushandle"></div>
      <div
        class="bofang"
        @click="changepaused"
        :class="{ active: paused }"
      ></div>
      <div class="next" @click="nexthandle"></div>
      <div class="history"></div>
    </div>
  </div>
</template>

<script>
import Lyric from '@/components/Lyric.vue';
export default {
  components: { Lyric },
  props: ["newdata", "duration", "currentTime", "audio","paused","newmusic","newlyric"],
  data() {
    return {
      // lipaused: this.paused,
      previous: 0,
      previousmusic:null,
      ishowLyric:true
      // linewid: this.newid,
    };
  },

  methods: {
    // 上一曲
    Previoushandle:function(){
      this.newmusic.map((item,index)=>{
        // console.log(item);
        if(item.id==this.newdata.id){
          index--;
          console.log(item.l);
          index=index<0?this.newmusic.length-1:index;
          return this.previous= index;
        }
      });
      this.previousmusic=this.newmusic[this.previous];
      this.$emit('previousitem',this.previousmusic);
    },
    //下一曲
    nexthandle:function(){
      this.newmusic.map((item,index)=>{
        // console.log(item);
        if(item.id==this.newdata.id){
          index++;
          index=index>this.newmusic.length-1?0:index;
          return this.previous= index;
        }
      });
      this.previousmusic=this.newmusic[this.previous];
      this.$emit('nextitem',this.previousmusic);
    },
    getValue: function (e) {
      // console.log(e.target.value);
      this.$emit("update:currentTime", e.target.value);
    },
    // 改变音乐播放
    changepaused: function () {
      this.$emit('changepaused');
      // if (this.lipaused) {
      //   this.audio.pause();
      //   this.lipaused = false;
      // } else {
      //   this.audio.play();
      //   this.lipaused = true;
      // }
    },
    //格式化事件
  },
  computed: {
    current: function () {
      var min = Math.floor(this.currentTime / 60).toFixed();
      var second = Math.floor(this.currentTime % 60).toFixed();
      second = second < 10 ? "0" + second : second;
      min = min < 10 ? "0" + min : min;
      return min + ":" + second;
    },
    durat: function () {
      var min = Math.floor(this.duration / 60).toFixed();
      var second = Math.floor(this.duration % 60).toFixed();
      second = second < 10 ? "0" + second : second;
      min = min < 10 ? "0" + min : min;
      return min + ":" + second;
    },
  },
};
</script>

<style lang="less">
.Songlistdetails {
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  color: #fff;
  padding: 15px 15px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1000;
  .Masked{
    width: 100vw;
    height: 100vh;
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    background: rgba(205, 224, 230, 0.5);
  }
  .bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(36px) brightness(80%);
    // background: ;
    z-index: -2;
  }
  .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 0px 10px 0px;
    .pull-down {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/huabanfuben.png")
        no-repeat;
      background-size: 25px 25px;
    }
    .name {
      display: flex;
      flex-direction: column;
      align-items: center;
      span {
        display: inline-block;
        text-align: center;
        &:first-child {
          max-width: 150px;
          font-size: 14px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        &:last-child {
          max-width: 150px;
          text-indent: 0.2em;
          font-size: 12px;
          color: #888;
          letter-spacing: 0.1em;
          position: relative;
        }
        &:last-child::after {
          position: absolute;
          right: -25%;
          top: 25%;
          content: "";
          width: 1em;
          height: 1em;
          background: url("../../public/img/icon_boszdaxao/xiangqing.png")
            no-repeat;
          background-size: 10px 10px;
          background-position: 50% 50%;
          margin-top: -0.15em;
        }
      }
    }
    .share {
      width: 30px;
      height: 30px;
      background: url("../../public/img/icon_boszdaxao/fenxiang.png") no-repeat;
      background-size: 35px 35px;
      background-position: 50% 50%;
    }
  }
  .newsong {
    flex: 0.85;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    &::after {
      content: "";
      width: 100px;
      height: 150px;
      background: url("../../public/img/icon_play.png") no-repeat;
      background-size: 80px 105px;
      position: absolute;
      top: 0;
      right: 0;
      margin-top: 0px;
      margin-right: 20vw;
    }
    .song {
      position: relative;
      img {
        width: 200px;
        height: 200px;
        border-radius: 50%;
      }
    }
    .song::after {
      content: "";
      width: 300px;
      height: 300px;
      background: url("../../public/img/black1.png") no-repeat;
      background-size: 300px 300px;
      position: absolute;
      top: 0;
      left: 0;
      margin-top: -50px;
      margin-left: -50px;
    }
  }
  .iron {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    .heart {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/aixin.png") no-repeat;
      background-size: 25px 25px;
    }
    .download {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/xiazai.png") no-repeat;
      background-size: 25px 25px;
    }
    .sing {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/changpian.png") no-repeat;
      background-size: 25px 25px;
    }
    .comment {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/buoumaotubiao48.png")
        no-repeat;
      background-size: 25px 25px;
    }
    .more {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/gengduo.png") no-repeat;
      background-size: 25px 25px;
    }
  }
  .input {
    margin: 20px 0px;
    display: flex;
    justify-content: space-between;
    justify-items: center;
    span {
      display: inline-block;
      // width: 40px;
      font-size: 12px;
      padding: 0 6px;
    }
    input {
      flex: 1;
      height: 5px;
      line-height: 5px;
      border-radius: 10px;
       -webkit-appearance: none;
       margin: 5px 0;
      &::-webkit-slider-runnable-track {
        height: 2.5px;
        border-radius: 2.5px; /*将轨道设为圆角的*/
        // box-shadow: 0 1px 1px #def3f8, inset 0 0.125em 0.125em #0d1112; /*轨道内置阴影效果*/
      }
      &::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 10px;
        width: 10px;
        margin-top: -3.5px; /*使滑块超出轨道部分的偏移量相等*/
        background: #ffffff;
        border-radius: 50%; /*外观设置为圆形*/
        border: solid 0.125em rgba(205, 224, 230, 0.5); /*设置边框*/
        box-shadow: 0 0.125em 0.125em #3b4547; /*添加底部阴影*/
      }
    }
    input[type="range"]::-webkit-slider-thumb {
      -webkit-appearance: none;
    }
  }
  .control {
    flex: 0.2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .pattern {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/inturn.png") no-repeat;
      background-size: 25px 25px;
    }
    .Previous {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/shangyishou.png")
        no-repeat;
      background-size: 25px 25px;
    }
    .bofang {
      width: 80px;
      height: 80px;
      border: 2px solid #fff;
      border-radius: 50%;
      background: url("../../public/img/icon_boszdaxao/zanting_1.png") no-repeat;
      background-size: 25px 25px;
      background-position: 50% 50%;
      &.active {
        background: url("../../public/img/icon_boszdaxao/zantingtingzhi.png")
          no-repeat;
        background-size: 25px 25px;
        background-position: 50% 50%;
      }
    }
    .next {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/shangyishou.png")
        no-repeat;
      background-size: 25px 25px;

      transform: rotate(180deg);
    }
    .history {
      width: 25px;
      height: 25px;
      background: url("../../public/img/icon_boszdaxao/play_record.png")
        no-repeat;
      background-size: 25px 25px;
    }
  }
}
</style>