<template>
  <div class="home">
    <Titlecomponent>推荐歌单</Titlecomponent>
    <ul class="component">
      <Liitem
        v-for="item in currentRecomends"
        :key="item.id"
        :item="item"
        :id="item.id"
        @recomment="recomment"
      />
    </ul>
    <Titlecomponent>最新音乐</Titlecomponent>
    <Newmusic
      :newmusictwo="newmusictwo"
      @newmusicone="newmusicid"
      :paused="$attrs.paused"
      :newindex="$attrs.newindex"
      :newmusic="$attrs.newmusic"
     
    />
  </div>
</template>
    
<script>
import Liitem from "@/components/Liitem.vue";
import Titlecomponent from "../components/Titlecomponent.vue";
import Newmusic from "../components/Newmusic.vue";

export default {
  data: function () {
    return {
      recomments: [],
      newid: [],
      newsongstr: "",
      newmusictwo: [],
      recommentsindex: 0,
      count: 0,
      // 推荐歌单数据
      recommentsong: [],
      recomentid: [],
      recomentstr: "",
      itemId: 0,
      recommentcreator: null,
      countid: 0,
    };
  },
  methods: {
    //推荐歌单歌曲
    recommentsongone: function (nid) {
      this.axios
        .get("/playlist/detail?id=" + nid)
        .then((response) => {
          console.log(response.data);
          this.recommentcreator = response.data.playlist;
          this.recomentid = response.data.playlist.trackIds.slice(0, 20);
          this.recomentid = this.recomentid.map((item) => {
            return item.id;
          });

          this.recomentstr = this.recomentid.join(",");
          this.axios
            .get("/song/detail?ids=" + this.recomentstr)
            .then((response) => {
              return (this.recommentsong = response.data.songs);
            })
            .finally(() => {
              this.countid++;
            });
        })
        .finally(() => {
          this.countid++;
        });
    },
    // 当前歌曲的id
    newmusicid: function (item, index, newmusic) {
      this.$emit("newmusicone", item, index, newmusic);
    },
    // 传递推荐歌单以及id
    recomment: function (a, b, c) {
      this.$emit("recomment", a, b, c);
      this.itemId = a.id;
      console.log(a.id);
    },
  },
  // 当前展示的6个歌单
  computed: {
    currentRecomends: function () {
      return this.recomments.slice(
        this.recommentsindex * 6,
        (this.recommentsindex + 1) * 6
      );
    },
  },
  // 一开始创造歌单和获取最新音乐
  created() {
    this.$root.isshowmuic = true;
    // 获取推荐歌单
    this.axios
      .get("personalized")
      .then((response) => {
        console.log(response.data);
        this.recomments = response.data.result;
      })
      .finally(() => {
        this.count++;
      });
    // 获取最新音乐
    this.axios
      .get("personalized/newsong")
      .then((response) => {
        this.newid = response.data.result.slice(0, 20);
        this.newid = this.newid.map((item) => {
          return item.id;
        });

        this.newsongstr = this.newid.join(",");
        this.axios
          .get("/song/detail?ids=" + this.newsongstr)
          .then((response) => {
            return (this.newmusictwo = response.data.songs);
          })
          .finally(() => {
            this.count++;
          });
      })
      .finally(() => {
        this.count++;
      });
  },
  watch: {
    // 监听count背景图片隐藏
    count: function (n) {
      if (n == 3) {
        var _this = this;
        setTimeout(function () {
          _this.$root.isshowmuic = false;
        }, 500);
      }
    },
    // 监听推荐歌单id变化
    // itemId: function () {
    //   // 调用方法找出当前歌单的所有歌曲
    //   this.recommentsongone(this.itemId);
    // },
  },
  // keep active 配合做的
  activated() {
    this.recommentsindex =
      this.recommentsindex >= 4 ? 0 : this.recommentsindex + 1;
    // console.log("切换");
  },
  components: {
    Liitem,
    Titlecomponent,
    Newmusic,
  },
};
</script>
<style scoped lang="less">
.home {
  .component {
    display: flex;
    flex-wrap: wrap;
  }
}
</style>
