import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Search from '../views/Search.vue'
import Hot from '../views/Hot.vue'
import Songdedail from '../views/Songdedail.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta:{isShowNav:true},

  },
  {
    path: '/Hot',
    name: 'Hot',
    component: Hot,
    meta:{isShowNav:true},

  },
  {
    path: '/Search',
    name: 'Search',
    component: Search,
    meta:{isShowNav:true},

  },
  {
    path: '/Songdedail',
    name: 'Songdedail',
    component: Songdedail,
  }
]

const router = new VueRouter({
  routes
})

export default router
