<template>
  <div class="audio">
    <audio
      :src="
        'https://music.163.com/song/media/outer/url?id=' + newdata.id + '.mp3'
      "
      controls
      autoplay
      ref="audio"
    ></audio>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
      @previousitem="$emit('previousitem'), $event"
    >
      <div v-show="!isshowSongList">
        <div class="play" @click="isshowSongList = true">
          <img :src="newdata.al.picUrl" alt="" />
          <div class="artists">
            <span>{{ newdata.name }}</span>
            <span>-{{ newdata.ar[0].name }}</span>
          </div>
          <div class="iron_box" @click.stop="controlPlayback">
            <div class="box">
              <div class="iron" :class="{ active: paused }"></div>
              <canvas ref="circle" width="50px" height="50px"></canvas>
            </div>
          </div>
        </div>
        <!-- 遮罩层 -->
      </div>
    </transition>

    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <div v-if="isshowSongList" @touchmove.prevent>
        <SongListDetails
          @isshowSongList="isshowSongListshow"
          :newdata="newdata"
          :duration="duration"
          :audio="$refs.audio"
          :currentTime="currentTime"
          :paused="paused"
          @update:currentTime="consolecurrentTime"
          :newmusic="newmusic"
          @previousitem="$emit('previousitem', $event)"
          @nextitem="$emit('nextitem', $event)"
          @changepaused="changepaused"
          :newlyric="newlyric"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import SongListDetails from "./SongListDetails.vue";
export default {
  components: { SongListDetails },
  props: ["newdata", "newmusic", "newmusicid"],
  data() {
    return {
      paused: null,
      duration: 0,
      currentTime: 0,
      isshowSongList: null,
      // newlyric:null,
      newlyric: null,
    };
  },
  mounted() {
    let audio = this.$refs.audio;
    this.$refs.audio.addEventListener("pause", () => {
      this.paused = false;
    });
    this.$refs.audio.addEventListener("play", () => {
      this.paused = true;
    });
    this.$refs.audio.addEventListener("durationchange", () => {
      this.duration = audio.duration;
    });
    this.$refs.audio.addEventListener("timeupdate", () => {
      this.currentTime = audio.currentTime;
      this.drawCircle(this.currentTime, this.duration);
    });
    this.$refs.audio.addEventListener("ended", () => {
      this.$emit("nextsong");
    });
    this.$refs.audio.addEventListener("loadstart", () => {
      var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
      this.axios.get("/lyric?id=" + this.newmusicid).then((response) => {
        this.newlyric = response.data.lrc.lyric;

        this.newlyric = this.newlyric
          .split("\n")
          .filter((e) => e)
          .map((str) => {
            var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
            var timeArr = time.split(":");
            time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
            var text = str.replace(patt, "");
            return { time, text };
          });
      });
    });
  },
  // updated() {
  //   var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
  //   this.axios.get("/lyric?id=" + this.newmusicid).then((response) => {
  //     this.newlyric = response.data.lrc.lyric;

  //     this.newlyric = this.newlyric
  //       .split("\n")
  //       .filter((e) => e)
  //       .map((str) => {
  //         var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
  //         var timeArr = time.split(":");
  //         time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
  //         var text = str.replace(patt, "");
  //         return { time, text };
  //       });
  //   });
  // },
  methods: {
    // SongListDetails控制播放
    changepaused: function () {
      if (this.paused) {
        this.$refs.audio.pause();
      } else {
        this.$refs.audio.play();
      }
    },

    // updated() {
    //       var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
    //       this.axios.get("/lyric?id=" + this.newmusicid).then((response) => {
    //        this.newlyric = response.data.lrc.lyric;
    //       });
    //        this.newlyric = this.newlyric.split("\n").filter((e) => e).map((str) => {
    //       var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
    //       var timeArr = time.split(":");
    //       time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
    //       var text = str.replace(patt, "");
    //       return { time, text };
    //     });
    //   console.log(this.newlyric);
    // },
    //上一曲
    // previousitem:function () {
    //     this.$emit('previousitem',item)
    // },
    //画圆进度条
    drawCircle: function (n, total) {
      let canvas = this.$refs.circle;
      let ctx = canvas.getContext("2d");
      ctx.clearRect(0, 0, 50, 50);
      ctx.beginPath();
      ctx.strokeStyle = "rgba(0,0,0,0.3)";
      ctx.arc(25, 25, 20, 0, 2 * Math.PI);
      ctx.lineWidth = 1;
      ctx.stroke();
      ctx.closePath();

      ctx.beginPath();
      ctx.strokeStyle = "#fff";
      ctx.lineWidth = 2;
      ctx.arc(25, 25, 20, -0.5 * Math.PI, ((n / total) * 2 - 0.5) * Math.PI);
      ctx.stroke();
    },
    //控制播放
    controlPlayback: function () {
      if (this.paused) {
        this.$refs.audio.pause();
      } else {
        this.$refs.audio.play();
      }
    },
    //歌单详情显示
    isshowSongListshow: function (n) {
      this.isshowSongList = n;
    },
    consolecurrentTime: function (n) {
      this.$refs.audio.currentTime = n;
    },
  },
  watch: {
    paused: function (n) {
      this.$emit("update:paused", n);
    },
  },
};
</script>

<style lang="less">
.audio {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  // height: 18vh;
  // height: 100%;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  // background: white;
  audio {
    width: 100vw;
    height: 10vh;
    outline: none;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
  }
  .play {
    width: 100vw;
    height: 9vh;
    background: rgba(202, 187, 189, 80%);
    align-items: center;
    padding: 0px 15px;
    display: flex;
    flex-direction: row;
    position: relative;
    //  justify-content: space-around;
    img {
      display: inline-block;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      animation: palying 6s linear infinite;
      margin-bottom: 25px;
      box-shadow: 0 0 2px 0 #000;
    }

    .artists {
      // height: 1em;
      // width: 200px;
      // overflow: hidden;
      // white-space: nowrap;
      // text-overflow: ellipsis;
      span {
        display: inline-block;
        //  text-overflow: ellipsis;
        // width: 200px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        &:first-child {
          max-width: 140px;
          font-size: 16px;
          margin-left: 20px;
        }
        &:last-child {
          max-width: 60px;
          text-indent: 0.2em;
          font-size: 12px;
          color: #888;
          letter-spacing: 0.1em;
          margin-left: 5px;
        }
      }
    }
    .iron_box {
      justify-self: flex-end;
      position: absolute;
      right: 8%;
      top: 10%;
      width: 50px;
      height: 50px;

      .box {
        width: 50px;
        height: 50px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;

        .iron {
          width: 23px;
          height: 23px;
          background: url("../../public/img/icon_boszdaxao/bofang.png")
            no-repeat;
          background-position: 100% 50%;
          background-size: 18px 18px;
          &.active {
            width: 25px;
            height: 25px;
            background: url("../../public/img/icon_boszdaxao/zanting.png")
              no-repeat;
            background-position: 50% 50%;
            background-size: 20px 20px;
          }
        }
        canvas {
          width: 50px;
          height: 50px;
          position: absolute;
          left: 0;
          top: 0;
        }
      }
    }
  }
}
@keyframes palying {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>