<template>
  <ul>
    <li
      v-for="(item, index) in hotdate"
      :key="item.id"
      class="newmusic"
      @click="hotmusichadle(item,index,hotdate)"
    >
      <div class="num">
        <span
          v-text="index < 9 ? '0' + ++index : ++index"
          class="numindex"
          :class="{active:index<3}"
          
        ></span>
      </div>
      <div class="left">
        <div class="top">
          <span>{{ item.name }}</span>
        </div>
        <div class="bottom">
          <span>{{ item.ar[0].name }}-{{ item.al.name }}</span>
        </div>
      </div>
      <div class="right">
         <span :class="{'active': newindex==undefined?'':newmusic[newindex].id===item.id&&paused}"></span>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["hotdate", "paused","newindex","newmusic"],
  data() {
    return {
    };
  },
  methods: {
    hotmusichadle:function(item,index,hotdate){
      this.$emit('newmusicone',item,index-1,hotdate);
    }
  },
};
</script>

<style scoped lang="less">
ul {
  margin-bottom: 60px;
}
.newmusic {
  display: flex;
  margin-bottom: 10px;
  border-bottom: 1px solid #f8f8f9;

  .num {
    display: flex;
    flex: 1 0 10vw;
    justify-content: center;
    align-items: center;
    // background-color: blue;
    // &:nth-child(1){
    //   background: lawngreen;
    // }
    .numindex {
      display: inline-block;
      font-size: 16px;
      &.active {
        color: red;
      }
    }
    // .numindex:nth-of-type(0) {
    //   color: blue;
    // }
    // text-emphasis-color: ;
  }
  .left {
    flex: 1 0 80vw;
    display: flex;
    flex-direction: column;
    padding: 5px 0px;
    .top {
      span {
        display: inline-block;
        font-size: 18px;
        max-width: 60vw;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
    .bottom {
      span {
        display: inline-block;
        max-width: 60vw;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 10px;
        color: #888888;
        position: relative;
        padding-left: 15px;
        &::before {
          content: "";
          width: 12px;
          height: 1em;
          background: url("../../public/img/icon.png") no-repeat;
          background-size: 166px 97px;
          position: absolute;
          left: 0;
          top: 0.45em;
        }
      }
    }
  }
  .right {
    flex: 1 0 10vw;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      display: inline-block;
      width: 22px;
      height: 22px;
      background: url("../../public/img/icon.png") no-repeat;
      background-position: -24px 0;
      background-size: 166px 97px;
    }
    span.active {
      display: inline-block;
      width: 22px;
      height: 22px;
      background: url("../../public/img/Bars.gif");
      background-size: 22px 22px;
    }
  }
}
</style>