<template>
  <div
    id="app"
    @touchstart="movingstart"
    @touchmove="movingmove"
    @touchend="movingend"
  >
    <!-- <div class="loading" v-show="$parent.isshowmuic">
      <div class="imgmusic"></div>
    </div> -->
    <HomeNav v-if="$route.meta.isShowNav" />
    <Audio
      v-if="isshow"
      :newdata="newdata"
      @update:paused="paused = $event"
      :newmusic="newmusic"
      @previousitem="previousitem"
      @nextitem="nextitem"
      @nextsong="nextsongdate"
      :newmusicid="newmusicid"
    />
    <keep-alive>
      <router-view
        @newmusicone="appNewMusic"
        v-bind:paused="paused"
        :newindex="newindex"
        :newmusic="newmusic"
      @recomment='apprecommenthandle'
      :apprecommentobj="apprecommentobj"
       :apprecommentitem="apprecommentitem"
      />
    </keep-alive>
  </div>
</template>
<script>
import HomeNav from "@/components/HomeNav.vue";
import Audio from "./components/Audio.vue";
export default {
  components: {
    HomeNav,
    Audio,
  },
  data() {
    return {
      newmusic: [],
      newdata: [],
      isshow: false,
      paused: null,
      currentIndex: 0,
      nextsong: null,
      startX: null,
      moveX: null,
      newmusicid: 0,
      newindex: undefined,
      // 推荐歌单的歌曲
      apprecommentobj: [],
      apprecommentitem:null,
      // newlyric: null,
    };
  },
  methods: {
    movingstart(e) {
      this.startX = e.touches[0].clientX;
    },
    movingmove(e) {
      this.moveX = e.touches[0].clientX;
    },
    movingend() {
      if (this.startX == null || this.moveX == null) {
        return;
      }
      let name = this.$route.name;
      // console.log(name);

      switch (name) {
        case "Home":
          if (this.moveX < this.startX - 150) {
            this.$router.push({
              path: "/Hot",
            });
          }
          break;
        case "Hot":
          if (this.moveX < this.startX - 150) {
            this.$router.push({
              path: "/Search",
            });
          } else if (this.moveX > this.startX + 150) {
            this.$router.push({
              path: "/",
            });
          }
          break;
        case "Search":
          if (this.moveX > this.startX + 150) {
            this.$router.push({
              path: "/Hot",
            });
          }
          break;
      }
      this.moveX = null;
      this.startX = null;
    },
    // 推荐歌单处理
    apprecommenthandle: function (item,itemtwo,recomentsong){
        console.log(item);
      this.apprecommentobj = itemtwo;
      this.apprecommentitem = recomentsong;
      
    },
    //自动播放下一首
    nextsongdate: function () {
      this.newmusic.map((item, index) => {
        if (item.id == this.newdata.id) {
          index++;
          index = index > this.newmusic.length - 1 ? 0 : index;
          return (this.currentIndex = index);
        }
      });
      this.nextsong = this.newmusic[this.currentIndex];
      this.newdata = this.nextsong;
      this.newmusicid = this.newdata.id;
      this.newindex++;
    },
    //上一首下一首
    previousitem: function (previous) {
      this.newdata = previous;
      this.newmusicid = this.newdata.id;
      this.newindex--;
    },
    nextitem: function (previous) {
      this.newdata = previous;
      this.newmusicid = this.newdata.id;
      this.newindex++;
    },
    appNewMusic: function (n, index, newmusic) {
      if (this.isshow) {
        this.isshow = true;
      } else {
        this.isshow = true;
      }
      this.newdata = n;
      this.newindex = index;
      this.newmusicid = this.newdata.id;
      this.newmusic = newmusic;
      // console.log(this.newlyric);
    },
    // 歌词
    lyric: function () {
      //  var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
      this.axios.get("/lyric?id=" + this.newmusicid).then((response) => {
        this.newlyric = response.data.lrc.lyric;
      });
    },
  },
};
</script>
<style scoped lang="less">
#app {
  .loading {
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: 999;
    left: 0;
    top: 0;
    background-size: 100vw 100vh;
    // background-image: linear-gradient(direction, color-stop1, color-stop2, ...);
    background-image: linear-gradient(to bottom, #c44443, #cf3334);
    // background-color: #C53335;
    .imgmusic {
      width: 100vw;
      height: 100vh;
      position: absolute;
      z-index: 1000;
      left: 0;
      top: 0;
      background: url("../public/img/bgmusic.jpg") no-repeat center center;
      background-size: 100vw 60vh;
    }
  }
}
:root {
  --animate-duration: 400ms;
}
</style>
 