<template>
  <div v-show="apprecommentobj">
    <div class="container">
      <div
        class="bg"
        :style="`background:url('${apprecommentobj.coverImgUrl}');background-repeat: no-repeat;background-size: 100vw 20vh;background-size: cover;background-position: 50%;`"
      ></div>
      <div class="main">
        <div class="left_img">
          <img :src="apprecommentobj.coverImgUrl" alt="" />
          <span class="song">歌单</span>
          <i>{{ apprecommentobj.playCount | CountName }}</i>
        </div>
        <div class="right">
          <div class="tittle">
            {{ apprecommentobj.name }}
          </div>
          <div class="author">
            <img :src="apprecommentobj.creator.avatarUrl" alt="" />
            <span>{{ apprecommentobj.creator.nickname }}</span>
          </div>
        </div>
      </div>
    </div>
    <Recomentsong
      :apprecommentitem="$attrs.apprecommentitem"
      @newmusicone="newmusicid"
      :paused="$attrs.paused"
      :newindex="$attrs.newindex"
      :newmusic="$attrs.newmusic"
    />
  </div>
</template>

<script>
import Recomentsong from "@/components/Recomentsong.vue";
export default {
  components: { Recomentsong },
  props: ["apprecommentobj"],
  methods: {
    newmusicid: function (item, index, apprecommentitem) {
      this.$emit("newmusicone", item, index, apprecommentitem);
    },
  },
  filters: {
    CountName: function (playCount) {
      if (playCount > 100000000) {
        return (playCount / 100000000).toFixed(1) + "亿";
      } else {
        return (playCount / 10000).toFixed(1) + "万";
      }
    },
  },
};
</script>

<style lang="less">
.container {
  width: 100vw;
  height: 25vh;
  position: relative;
  overflow: hidden;
  .bg {
    width: 100%;
    height: 20vh;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    filter: blur(20px);
    transform: scale(1.5);
  }
  .bg::after {
    content: " ";
    width: 100vw;
    height: 20vh;
    background-color: rgba(0, 0, 0, 0.25);
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
  }
  .main {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    z-index: 5;
    padding: 20px 10px;
    .left_img {
      position: relative;
      flex: 1 0 0.5;
      img {
        width: 8.5em;
        min-width: 7.5em;
        height: 8.5em;
      }
      span {
        position: absolute;
        z-index: 3;
        top: 10px;
        left: 0;
        padding: 0 8px;
        height: 17px;
        color: #fff;
        font-size: 9px;
        text-align: center;
        line-height: 17px;
        background-color: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 17px;
        border-bottom-right-radius: 17px;
      }
        i {
        display: inline-block;
        font-size: 12px;
        color: white;
        position: absolute;
        right: 0px;
        top: 5px;
        padding-left: 2px;
        &::before {
          content: "";
          height: 1em;
          width: 1em;
          background: url(data:image/svg+xml;base64,)
            no-repeat;
          position: absolute;
          left: -1em;
          top: 0.25em;
        }
        }
    }
    .right {
      -webkit-box-flex: 1;
    -webkit-flex: 1 1 auto;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    width: 1%;
    margin-left: 16px;
      .tittle {
        padding-top: 1px;
    font-size: 17px;
    line-height: 1.3;
    color: #fefefe;
    height: 44px;
    display: -webkit-box;
    -webkit-box-pack: center;
      }
      .author {
        margin-top: 20px;
        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          vertical-align: middle;
          margin-right: 5px;
        }
        span {
          color: hsla(0,0%,100%,.7);
        }
      }
    }
  }
}
</style>