<template>
  <div class="search">
    <div class="loading" v-show="isshowloading"></div>
    <!-- input搜索标签 -->
    <div class="top_input">
      <input
        type="text"
        @change="handleText"
        v-model="text"
        @focus="focusinput"
        placeholder="搜索歌曲、歌手、专辑"
      />
      <div class="deload">
        <span></span>
      </div>
      <div class="close" @click="deletetext">
        <span></span>
      </div>
    </div>
    <!-- 搜索列表 -->
    <div v-show="isshowkeyword" class="Vaguesearch">
      <div @click="searchText" class="Vaguesearchname">
        搜索:"{{ this.text }}"
      </div>
      <ul>
        <li
          v-for="item in searchname"
          :key="item.id"
          @click="handleli(item.keyword)"
        >
          <div class="search"></div>
          {{ item.keyword }}
        </li>
      </ul>
    </div>
    <!-- 搜索内容 -->
     
    <Searchmusic
      :searchnewmusic="searchnewmusic"
     @newmusicone="newmusicid"
      :paused="$attrs.paused"
      :newindex="$attrs.newindex"
      :newmusic="$attrs.newmusic"
      v-show="isshow"
      :searchalbum="searchalbum"
      :ishowalbum="ishowalbum"
      :searchmain="searchmain"
    />
    <!-- 热门搜索列表-->
    <div class="hotsearch" v-show="isshowhot">
      <div class="hotname">热门搜索</div>
      <div class="searchloading" v-show="isshowloadingmusic"></div>
      <ul>
        <li
          v-for="(item,id) in searchhot"
          :key="id"
          @click="hothadle(item.first)"
        >
          {{ item.first }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Searchmusic from "../components/Searchmusic.vue";
export default {
  components: { Searchmusic },
  data() {
    return {
      text: "",
      searchdata: [],
      searchidstr: "",
      searchnewmusic: [],
      isshow: false,
      searchname: [],
      keyword: "",
      isshowkeyword: false,
      searchalbum: [],
      ishowalbum: false,
      searchhot: [],
      isshowhot: true,
      isshowloading:false,
      searchmain:false,
      isshowloadingmusic:false,
    };
  },
  //热门搜索Hot数据
  created() {
    this.isshowloadingmusic = true;
    this.axios.get("/search/hot").then((response) => {
      return (this.searchhot = response.data.result.hots);
    }).finally(()=>{
      this.isshowloadingmusic = false;
    });
  },
  // 监听text值的变化
  watch: {
    text: function () {
      if (this.text) {
        this.handlevalue();
        this.isshowhot = false;
        if (this.isshow == false) {
          this.isshowkeyword = true;
        }
      } else {
        this.isshowhot = true;
        this.isshow = false;
        this.isshowkeyword = false;
      }
    },
  },
  methods: {
      newmusicid: function (item, index, newmusic) {
      this.$emit("newmusicone", item, index, newmusic);
    },
    // 鼠标聚焦focus事件input标签
    focusinput: function () {
      if (this.isshow) {
        this.isshow = false;
        this.isshowkeyword = true;
        this.isshowhot = false;
       
      }
       this.searchmain = false;
    },
    // 鼠标点击搜索
    searchText: function () {
      this.keyword = this.text;
      this.isshow = true;
      this.isshowkeyword = false;
      this.isshowhot = false;
      this.handleText();
    },
    // 鼠标点击删除图标删除input/value值
    deletetext: function () {
      this.text = "";
      this.keyword="";
      this.handleText();
    },
    //热门搜索单独盒子点击事件
    hothadle: function (hotvalue) {
      this.text = hotvalue;
      this.keyword = hotvalue;
      this.isshow = true;
      this.isshowkeyword = false;
      this.isshowhot = false;
      this.handleText();
    },
    //模糊搜索值
    handleli: function (keyword) {
      this.keyword = keyword;
      this.isshow = true;
      this.isshowkeyword = false;
      this.isshowhot = false;
      this.handleText();
    },
    // 处理this.text的模糊搜索数据
    handlevalue: function () {
      this.axios
        .get("/search/suggest?keywords=%20" + this.text + "&type=mobile")
        .then((response) => {
          this.searchname = response.data.result.allMatch;
        });
    },
    handleText: function () {
      if(this.keyword==""){
         this.ishowalbum = false;
         this.isshow = false;
        return
      }
      this.isshowloading=true;
      this.searchmain=false;
      this.axios
        .get("/search/multimatch?keywords=%20" + this.keyword)
        .then((response) => {
          this.searchalbum = [];
          if (
            response.data.result.orders.length == 0 ||
            response.data.result.album == undefined
          ) {
            console.log("无");
            this.ishowalbum = false;
            return;
          }
          console.log(response.data.result.album);
          this.searchalbum = response.data.result.album.slice(0, 1);
          this.ishowalbum = true;
          this.isshowloading=false;
        });
      this.axios.get("/search?keywords=" + this.keyword).then((response) => {
        this.searchdata = response.data.result.songs;
        this.searchdata = this.searchdata.slice(0, 20).map((e) => {
          return e.id;
        });
        this.searchidstr = this.searchdata.join(",");
        this.axios
          .get("/song/detail?ids=" + this.searchidstr)
          .then((response) => {
             this.isshowloading=false;
             this.searchmain=true;
            return (this.searchnewmusic = response.data.songs);
          });
      });
    },
  },
};
</script>

<style lang="less">
.search {
    .searchloading {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 45%;
    margin-top: 20px;
    background: url("../../public/img/music.svg") center center;
    background-size: 50px 50px;
  }
  .loading{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    left: 50%;
    top:25%;
    margin-left: -10px;
    margin-top: -10px;
    background: url('../../public/img/music.svg') center center;
    background-size: 50px 50px;
  }
  display: flex;
  flex-direction: column;
  // input标签
  .top_input {
    position: relative;
    display: flex;
    margin: 15px auto;

    input {
      text-indent: 1.5em;
      width: 85vw;
      height: 30px;
      outline: none;
      border: 0;
      background-color: #ebecec;
      // color: #d0c9d0;
    }
    ::-webkit-input-placeholder {
      /* WebKit browsers */
      color: #d0c9d0;
      font-size: 14px;
    }
    .deload {
      width: 30px;
      height: 30px;
      background: #ebecec;
      border-radius: 50%;
      position: absolute;
      left: -2.9%;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        width: 14px;
        height: 14px;
        background: url("../../public/img/deload.svg") no-repeat;
        background-size: 14px 14px;
      }
    }
    .close {
      width: 30px;
      height: 30px;
      background: #ebecec;
      border-radius: 50%;
      position: absolute;
      right: -2.9%;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        display: block;
        width: 14px;
        height: 14px;
        background: url("../../public/img/close.svg") no-repeat;
        background-size: 14px 14px;
      }
    }
  }
  //热门搜索
  .hotsearch {
    border-top: 1px solid #eeeeef;
    padding: 10px;
    .hotname {
      color: #666666;
      font-size: 12px;
    }
    ul {
      display: flex;
      flex-wrap: wrap;
      // flex-direction: column;
      margin-top: 10px;
      li {
        // width: 50px;
        font-size: 14px;
        color: #333333;
        margin-right: 8px;
        margin-bottom: 8px;
        padding: 5px 12px;
        border: 1px solid #ccc;
        border-radius: 60px;
        // line-height: ;
      }
    }
  }
  //模糊查询
  .Vaguesearch {
    border-top: 1px solid #eeeeef;
    display: flex;
    flex-direction: column;
    padding: 0px 0px 0px 10px;
    .Vaguesearchname {
      padding: 5px 0px;
      color: #507daf;
      border-bottom: 1px solid #eeeeef;
    }
    ul {
      li {
        position: relative;
        div {
          position: absolute;
          left: -6%;
          top: 35%;
          width: 14px;
          height: 14px;
          background: url("../../public/img/deload.svg") no-repeat;
          background-size: 14px 14px;
        }
        margin-left: 25px;
        padding: 8px 0px 8px 0px;
        border-bottom: 1px solid #eeeeef;
      }
    }
  }
}
</style>