<template>
  <li class="itemli" @click="recommentsongone(id)">
    <div class="img">
      <img :src="item.picUrl" alt="" />
      <span>{{ item.playCount | CountName }}</span>
    </div>
    <h6>{{ item.name }}</h6>
  </li>
</template>

<script>
export default {
  props: ["item", "id"],
  data() {
    return {
      recommentsong: [],
      recomentid: [],
      recomentstr: "",
      recommentcreator: null,
    };
  },
  filters: {
    CountName: function (playCount) {
      if (playCount > 100000000) {
        return (playCount / 100000000).toFixed(1) + "亿";
      } else {
        return (playCount / 10000).toFixed(1) + "万";
      }
    },
  },
  methods: {
     recommentsongone: function (nid) {
      this.axios
        .get("/playlist/detail?id=" + nid)
        .then((response) => {
          console.log(response.data);
          this.recommentcreator = response.data.playlist;
          this.recomentid = response.data.playlist.trackIds.slice(0, 20);
          this.recomentid = this.recomentid.map((item) => {
            return item.id;
          });

          this.recomentstr = this.recomentid.join(",");
          this.axios
            .get("/song/detail?ids=" + this.recomentstr)
            .then((response) => {
              return (this.recommentsong = response.data.songs);
            }).finally(()=>{
              this.liitem();
            })
        })
      },

      
    liitem: function (id) {
      console.log(id);
      // this.recommentsongone(id)
      //传输的数据
      console.log(this.recommentsong);
  
        this.$emit(
          "recomment",
          this.item,
          this.recommentcreator,
          this.recommentsong
        );
         this.$router.push({
          path: "/Songdedail",
        });
      
    },
  },
};
</script>

<style scoped lang="less">
.itemli {
  flex: 1 0 30vw;
  font-size: 14px;
  margin-bottom: 10px;
  text-align: left;
  position: relative;
  &:nth-of-type(3n + 2) {
    margin: 0 5px;
  }

  .img {
    min-height: 33vw;
    span {
      display: inline-block;
      font-size: 12px;
      color: white;
      position: absolute;
      right: 0px;
      top: 5px;
      padding-left: 2px;
      &::before {
        content: "";
        height: 1em;
        width: 1em;
        background: url(data:image/svg+xml;base64,)
          no-repeat;
        position: absolute;
        left: -1em;
        top: 0.25em;
      }
    }
  }
  h6 {
    padding: 5px 0px 0px 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}
</style>